<template>
  <div>
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link to="/index" class="text-tertiary"></router-link>
        </li>
        <li class="breadcrumb-item">
          <router-link to="/products" class="text-tertiary"></router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">{{ cusProduct.title }}</li>
      </ol>
    </nav>
    <div class="row no-gutters">
      <div class="col-md-7">
        <div class="bg_cover cusproduct_header_img"
        :style="{'background-image': `url(${ cusProduct.imageUrl })`}"></div>
      </div>
      <div class="col-md-5 d-flex flex-column justify-content-between bg-light
      p-4 cusproduct_header_text">
        <h2 class="text-center font-weight-bold">{{ cusProduct.title }}</h2>
        <h6>{{ cusProduct.description }}</h6>
        <div class="d-flex flex-wrap justify-content-between align-items-baseline">
          <del> {{ cusProduct.origin_price }} </del>
          <div class="h5 text-secondary font-weight-bold"> {{ cusProduct.price }} </div>
        </div>
        <div class="d-flex justify-content-between align-items-center">
          <span class="h4"></span>
          <select class="cusproduct_select rounded pl-2" v-model="qty">
            <option v-for="i in 10" :key="i" :value="i">{{ i }}</option>
          </select>
        </div>
        <div class="d-flex justify-content-between">
          <button
            type="button"
            class="btn btn-outline-tertiary btn_fav"
            @click="addToCusFavs(cusProductId)"
            v-if="cusProduct.favored === false"
          ></button>
          <button
            type="button"
            class="btn btn-tertiary"
            @click="delCusFav(cusProductId)"
            v-if="cusProduct.favored === true"
          ></button>
          <button
            type="button"
            class="btn btn-secondary"
            v-if="carting === false"
            @click="addToCusCart(cusProduct)"
          ></button>
          <button
            type="button"
            class="btn btn-secondary"
            v-if="carting === true"
            disabled
          ></button>
        </div>
      </div>
    </div>
    <div class="row no-gutters pt-4">
      <div class="col-md-7 px-md-3">
        <h3 class="cusproduct_heading"></h3>
        <p class="cusproduct_content">{{ cusProduct.content }}</p>
        <h3 class="cusproduct_heading"></h3>
        <p class="cusproduct_content"></p>
        <h3 class="cusproduct_heading"></h3>
        <p class="cusproduct_content">
          <ul>
            <ol></ol>
            <ol></ol>
            <ol> 250c.c. </ol>
            <ol> 23 </ol>
            <ol></ol>
          </ul>
          <ul>
            <ol></ol>
            <ol> 600c.c. </ol>
            <ol></ol>
            <ol> 48 </ol>
          </ul>
        </p>
      </div>
      <div class="col-md-5 mb-4">
        <h3 class="rounded-top card_header"></h3>
        <products-card :bases="adProducts" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import productsCard from '../../components/productsCard';

export default {
  data() {
    return {
      cusProductId: '',
      cusProduct: {},
      qty: 1,
      adProducts: [],
    };
  },
  components: { productsCard },
  methods: {
    ...mapActions(['getCusProducts', 'addToCusFavs', 'delCusFav']),
    addToCusCart(cusProduct) {
      const { qty } = this;
      this.$store.dispatch('addToCusCart', { cusProduct, qty });
    },
    getCusProduct() {
      this.adProducts = [];
      const productsL = this.cusProducts.length;
      const stock = [];
      for (let i = 0; i < productsL; i++) {
        if (this.cusProducts[i].id === this.cusProductId) {
          this.cusProduct = this.cusProducts[i];
        } else {
          stock.push(this.cusProducts[i]);
        }
      }
      let i = stock.length;
      let index;
      const min = i - 4;
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random());
        [stock[i], stock[index]] = [stock[index], stock[i]];
      }
      this.adProducts = stock.slice(min);
    },
  },
  computed: {
    ...mapState(['cusProducts', 'carting']),
    watched() {
      const { cusProducts, cusProductId } = this;
      return { cusProducts, cusProductId };
    },
  },
  watch: {
    watched: {
      handler() {
        if (this.cusProducts.length > 1) {
          this.getCusProduct();
        }
      },
    },
  },
  created() {
    this.getCusProducts();
    this.cusProductId = this.$route.params.cusProductId;
    this.$store.commit('SET_CUSACTIVE', 'cusProducts');
  },
  beforeRouteUpdate(to, from, next) {
    this.getCusProducts();
    this.cusProductId = to.params.cusProductId;
    this.qty = 1;
    next();
  },
};
</script>

<style>
</style>



