<template>
  <div>
    <h3 class="admin_title"></h3>
    <div class="text-right mb-3 mr-3">
      <button class="btn btn-primary" @click="openCouponModal()"></button>
    </div>
    <div class="table-responsive mb-3">
      <table class="table text-center">
        <thead>
          <tr>
            <th class="admin_name"></th>
            <th class="admincoupons_discount"></th>
            <th class="admin_date"></th>
            <th class="admincoupons_code"></th>
            <th class="admin_enabled"></th>
            <th class="admin_edition"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="adminCoupon in adminCoupons" :key="adminCoupon.id">
            <td>{{ adminCoupon.title }}</td>
            <td>{{ adminCoupon.percent }}%</td>
            <td>{{ $displayDate(adminCoupon.due_date) }}</td>
            <td>{{ adminCoupon.code }}</td>
            <td>
              <span v-if="adminCoupon.is_enabled === 1" class="text-success"></span>
              <span v-else></span>
            </td>
            <td>
              <button
                class="btn btn-outline-primary btn-sm"
                @click="openCouponModal(adminCoupon)"
              ></button>
              <button
                class="btn btn-outline-danger btn-sm"
                @click="openDelCModal(adminCoupon)"
              ></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <pagination :pagination="pagination" @click-page="getAdminCoupons" class="ml-3"/>
    <!-- coupon Modal -->
    <div class="modal fade" id="couponModal" data-backdrop="static" data-keyboard="false"
      tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="addition"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder=""
                    v-model="tempCoupon.title"/>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="percent"></label>
                    <input type="number" class="form-control" id="percent"
                      v-model="tempCoupon.percent" placeholder="" />
                  </div>
                  <div class="form-group col-md-6">
                    <label></label>
                    <input class="form-control" id="cdd" @focus="pickDate" placeholder=""
                      :value="$displayDate(tempCoupon.due_date)"/>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="code"></label>
                    <input type="text" class="form-control" id="code"
                      v-model="tempCoupon.code" placeholder="" />
                  </div>
                </div>

                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled"
                      :true-value=1 :false-value=0 v-model="tempCoupon.is_enabled"/>
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-outline-black" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="uploadCoupon"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import pagination from '../../components/pagination';

export default {
  components: { pagination },
  data() {
    return {
      tempCoupon: {},
      addition: false,
    };
  },
  methods: {
    getAdminCoupons(page) {
      this.$store.dispatch('getAdminCoupons', page);
    },
    openCouponModal(adminCoupon) {
      if (adminCoupon) {
        this.tempCoupon = { ...adminCoupon };
        this.addition = false;
      } else {
        this.tempCoupon = {};
        this.addition = true;
      }
      $('#couponModal').modal('show');
    },
    pickDate() {
      const fp = flatpickr('#cdd', {});
      this.tempCoupon.due_date = Date.parse(fp.selectedDates[0]) / 1000;
    },
    uploadCoupon() {
      const vm = this;
      if (vm.addition) {
        const API = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
        vm.axios.post(API, { data: vm.tempCoupon }).then((response) => {
          if (response.data.success) {
            $('#couponModal').modal('hide');
            vm.getAdminCoupons();
          }
        });
      } else {
        const API = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        vm.axios.put(API, { data: vm.tempCoupon }).then((response) => {
          if (response.data.success) {
            $('#couponModal').modal('hide');
            vm.getAdminCoupons();
          }
        });
      }
    },
    openDelCModal(adminCoupon) {
      this.$store.commit('SET_MSG', {
        event: 'delAdminCoupon',
        object: adminCoupon.title,
        objectId: adminCoupon.id,
      });
    },
  },
  computed: {
    adminCoupons() {
      return this.$store.state.adminModule.adminCoupons;
    },
    pagination() {
      return this.$store.state.adminModule.pagination;
    },
  },
  created() {
    this.getAdminCoupons();
    this.$store.commit('SET_ADMINACTIVE', 'adminCoupons');
  },
};
</script>

<style scope>
</style>



