<template>
  <div>
    <h3 class="admin_title"></h3>
    <div class="text-right mb-3 mr-3">
      <button class="btn btn-primary" @click="openProductModal()"></button>
    </div>
    <div class="table-responsive mb-3">
      <table class="table">
        <thead>
          <tr class="text-center">
            <th class="adminproducts_cat"></th>
            <th class="admin_name"></th>
            <th class="adminproducts_oriprice"></th>
            <th class="adminproducts_price"></th>
            <th class="admin_enabled"></th>
            <th class="admin_edition"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="adminProduct in adminProducts" :key="adminProduct.id">
            <td class="text-center">{{ adminProduct.category }}</td>
            <td class="text-center">{{ adminProduct.title }}</td>
            <td class="text-right">{{ $displayCurrency(adminProduct.origin_price) }}</td>
            <td class="text-right">{{ $displayCurrency(adminProduct.price) }}</td>
            <td class="text-center">
              <span v-if="adminProduct.is_enabled === 1" class="text-success"></span>
              <span v-else></span>
            </td>
            <td class="text-center">
              <button
                class="btn btn-outline-primary btn-sm"
                @click="openProductModal(adminProduct)"
              ></button>
              <button
                class="btn btn-outline-danger btn-sm"
                @click="openDelModal(adminProduct)"
              ></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <pagination :pagination="pagination" @click-page="getAdminProducts" class="ml-3"/>
    <!-- product Modal -->
    <div class="modal fade" id="productModal" data-backdrop="static" data-keyboard="false"
      tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="addition"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <!--  -->
              <div class="col-sm-4 mb-3">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image" placeholder=""
                    v-model="tempProduct.imageUrl"/>
                </div>
                <div class="form-group">
                  <label for="customFile">
                    3MB
                    <i class="fas fa-spinner fa-spin" v-if="picUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files"
                    @change="uploadPic"/>
                </div>
                <div class="border border-danger p-3 text-center"
                  v-if="picErrorMsg"></div>
                <img class="img-fluid" :src="tempProduct.imageUrl" alt v-else/>
              </div>

              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder=""
                    v-model="tempProduct.title"/>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category" placeholder=""
                      v-model="tempProduct.category"/>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit" placeholder=""
                      v-model="tempProduct.unit"/>
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price"
                      v-model="tempProduct.origin_price" placeholder="" />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price"
                      v-model="tempProduct.price" placeholder="" />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description"
                    v-model="tempProduct.description" placeholder=""></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content"
                    v-model="tempProduct.content" placeholder=""></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled"
                      :true-value=1 :false-value=0 v-model="tempProduct.is_enabled"/>
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-outline-black" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary"
              @click="uploadProduct"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import pagination from '../../components/pagination';

export default {
  components: { pagination },
  data() {
    return {
      tempProduct: {},
      addition: false,
      picUploading: false,
      picErrorMsg: false,
    };
  },
  methods: {
    getAdminProducts(page) {
      this.$store.dispatch('getAdminProducts', page);
    },
    openProductModal(adminProduct) {
      if (adminProduct) {
        this.tempProduct = { ...adminProduct };
        this.addition = false;
      } else {
        this.tempProduct = {};
        this.addition = true;
      }
      $('#productModal').modal('show');
    },
    uploadProduct() {
      const vm = this;
      if (vm.addition) {
        const API = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
        vm.axios.post(API, { data: vm.tempProduct }).then((response) => {
          if (response.data.success) {
            $('#productModal').modal('hide');
            vm.getAdminProducts();
          }
        });
      } else {
        const API = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        vm.axios.put(API, { data: vm.tempProduct }).then((response) => {
          if (response.data.success) {
            $('#productModal').modal('hide');
            vm.getAdminProducts();
          }
        });
      }
    },
    uploadPic() {
      const API = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      const vm = this;
      vm.picUploading = true;
      const formData = new FormData();
      formData.append('file-to-upload', vm.$refs.files.files[0]);
      vm.axios.post(API, formData, {
        Headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((response) => {
        if (response.data.success) {
          vm.picUploading = false;
          vm.picErrorMsg = false;
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
        } else {
          vm.picUploading = false;
          vm.picErrorMsg = true;
        }
      });
    },
    openDelModal(adminProduct) {
      this.$store.commit('SET_MSG', {
        event: 'delAdminProduct',
        object: adminProduct.title,
        objectId: adminProduct.id,
      });
    },
  },
  computed: {
    adminProducts() {
      return this.$store.state.adminModule.adminProducts;
    },
    pagination() {
      return this.$store.state.adminModule.pagination;
    },
  },
  created() {
    this.getAdminProducts();
    this.$store.commit('SET_ADMINACTIVE', 'adminProducts');
  },
};
</script>

<style scope>

</style>



